@import url("https://fonts.googleapis.com/css2?family=Asap:wght@400;700&family=Raleway:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Asap:wght@400;700&family=Raleway:wght@700&display=swap");
.ButtonModal {
  width: 20%;
  background-color: #00904A;
  color: #FDFDFD;
  padding: 5px 0;
  outline: none;
  border: none;
  border-radius: 0.3em;
}

.WideButton {
  background-color: #00904A;
  color: #FDFDFD;
  padding: 5px 0;
  outline: none;
  border: none;
  border-radius: 0.3em;
  width: 90%;
}

h1, h2, h3, h4, h5, h6 {
  padding: 0;
  margin: 0;
}

h1#Profil {
  text-align: center;
}

p.edit_icon {
  color: #000000;
  margin-left: 10px;
}

img#UserPic {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

div#TextBody {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 50px;
}
div#TextBody div {
  padding: 10px 0 10px 50px;
}

.ProfileSpace {
  margin-bottom: 10px;
  margin-right: 10px;
}

.wrap_around {
  display: flex;
}